import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders} from '@angular/common/http';
import { GlobalEventsManagerService } from './global-events-manager.service';
import { IZonaBioAmbiental } from '../interfaces/IZonaBioAmbiental';
import { BEZonaBioAmbiental } from '../models/BEZonaBioAmbiental';
import { BaseService } from './base.service';
import { Observable } from 'rxjs/observable';
import { Subject } from 'rxjs/Subject';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';


const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};


@Injectable()

export class ZonaBioAmbientalService extends BaseService implements IZonaBioAmbiental {
  oMaterial = new Subject<any>();
  list: any = [];
  _baseUrl = '';

  constructor(private httpClient: HttpClient,private configService: GlobalEventsManagerService) {
    super();
    this._baseUrl = configService.getWebApiURL(); 
  }

  getZonas() {
  try{
    let _url = this._baseUrl + '/api/ZonaBioAmbiental/obtener-zonas/';
    return this.httpClient.get<BEZonaBioAmbiental[]>(_url).catch(this.handleError);
  }catch(err){
  console.log(err);
    }
  }

}
