import { Component, OnInit, EventEmitter, Input, Output } from '@angular/core';
import { HttpErrorResponse} from '@angular/common/http';
import { BEMaterial } from '../../models/BEMaterial';
import { MaterialService } from 'src/app/layout/services/material.service';
import { ZonaBioAmbientalService } from 'src/app/layout/services/zonaBioAmbiental.service';
import { Subscription } from 'rxjs/Subscription';
import { Message} from 'primeng/primeng';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import { Alert } from 'selenium-webdriver';
import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';
import { GlobalEventsManagerService } from 'src/app/layout/services/global-events-manager.service';
import { BEZonaBioAmbiental } from '../../models/BEZonaBioAmbiental';
import { CalculosService } from '../../services/calculos.service';
import { BEItem } from '../../models/BEItem';
import { BEResultado} from '../../models/BEResultado';
import { BEUnidadDeMedida} from '../../models/BEUnidadDeMedida';
import { BEMuroCubierta} from '../../models/BEItem';

@Component({
  selector: 'app-calculo-k',
  templateUrl: './calculo-k.component.html',
  styleUrls: ['./calculo-k.component.css']
})
export class CalculoKComponent implements OnInit {
  lstMateriales: Array<BEMaterial>;
  LstMuroCubierta: BEMuroCubierta[]=[];
  msgs: Message[] = [];
  subscription: Subscription;
  oSelectedMaterial: BEMaterial;
  lstZonas: Array<BEZonaBioAmbiental>;
  oSelectedZona: BEZonaBioAmbiental;
  selectedValueMuroCubierta: '';
  EspesorIngresado: 0;
  Resultado: BEResultado;
  ListaItems: Array<BEItem>;
  lstUnidadesDeMedida:Array<BEUnidadDeMedida>;
  oSelectedUnidadesDeMedida: BEUnidadDeMedida;

  @Input() display: boolean;
  @Output() displayChange = new EventEmitter();
  constructor( private _MaterialService: MaterialService, private _ZonaBioAmbientalService: ZonaBioAmbientalService, private _CalculosService: CalculosService) {
    
  }

  ngOnInit(): void {
    this.InicializarVariables();
    try { 

      this.LstMuroCubierta.push({Label:'Muro', Value: 'Muro'},{Label:'Cubierta', Value:'Cubierta'});
        this.lstUnidadesDeMedida.push(
          { IdUnidadDeMedida:1, Descripcion: "metro", EquivalenciaEnMetros:1, Sigla:"m"},
          { IdUnidadDeMedida:2, Descripcion: "centímetro", EquivalenciaEnMetros:Number(1/100), Sigla:"cm"},
          { IdUnidadDeMedida:3, Descripcion: "milímetro", EquivalenciaEnMetros:Number(1/1000), Sigla:"mm"}
        );
        this.oSelectedUnidadesDeMedida = this.lstUnidadesDeMedida[2];

          this.subscription = this._MaterialService.getMateriales().subscribe(
            ListaMateriales => {
                                  this.lstMateriales = ListaMateriales;
                                }
          );

          this.subscription = this._ZonaBioAmbientalService.getZonas().subscribe(
            ListaZonas => {
                                  this.lstZonas = ListaZonas;
                                  this.oSelectedZona = this.lstZonas.find(x=>x.NombreZona === 'IIIB');
                                }
          );

    }catch (err) {
      this.showError(err);
    }
  }
  showError(sErrorMensaje: string) {
    this.msgs = [];
    this.msgs.push({severity: 'error', summary: sErrorMensaje, detail: ''});
  }
  InicializarVariables(){
    this.EspesorIngresado=null;
    this.ListaItems = new Array<BEItem>();
    this.Resultado = new BEResultado();
    this.lstUnidadesDeMedida = new Array<BEUnidadDeMedida>();
  }
  AgregarItem(){
    this.msgs = [];
    if(!this.oSelectedMaterial) this.showError("Debe seleccionar un Material");
    if(!this.oSelectedZona) this.showError("Debe seleccionar una Zona Bio Ambiental");
    if(!this.EspesorIngresado) this.showError("Debe ingresar el espesor (expresado en metros) del material seleccionado");
    if(!this.selectedValueMuroCubierta) this.showError("Debe seleccionar Muro o Cubierta");
    if(this.msgs.length>0) return;
    
    let item = new BEItem();
    item.Espesor= Number(this.EspesorIngresado)* this.oSelectedUnidadesDeMedida.EquivalenciaEnMetros;
    item.IdMaterial = Number(this.oSelectedMaterial.IdMaterial);
    item.NombreMaterial = this.oSelectedMaterial.NombreMaterial;
    this.ListaItems.push(item);
  }
  Calcular(){
    
    try{
    
   
    this._CalculosService.CalcularResistenciaTermica(this.ListaItems, true).subscribe(
      resRT=>{
        this.Resultado.Resistencia_Termica = resRT.toString();
        this.Resultado.Resistencia_TermicaNumber = Number(parseFloat(this.Resultado.Resistencia_Termica.split(' ')[0].replace(',','.')).toFixed(4));
        this._CalculosService.CalcularTransmitancia(Number(this.Resultado.Resistencia_TermicaNumber)).subscribe(
          res=>{
            this.Resultado.Transmitancia = res.toString();
          }
        );

      }
    );
    
    
    this._CalculosService.CalcularMasa(this.ListaItems).subscribe(
      res=>{
            this.Resultado.Masa = res.toString();
            this.Resultado.MasaNumber = Number(parseFloat(this.Resultado.Masa.split(' ')[0].replace(',','.')).toFixed(4));
            var EsCubierta = false;
            EsCubierta = this.selectedValueMuroCubierta.includes("Cubierta");
            //MasaMuro: number, orientacion: string, zona: string, EsCubierta: boolean
            this._CalculosService.CalcularKMaxAdmisible(Number(this.Resultado.MasaNumber),'',this.oSelectedZona.NombreZona
            ,EsCubierta).subscribe(
              resKmax=>{
                this.Resultado.Maximo_K_Admisible = resKmax.toString();
                this.Resultado.Maximo_K_AdmisibleNumber = Number(parseFloat(this.Resultado.Maximo_K_Admisible.split(' ')[0].replace(',','.')).toFixed(4));
                this.Resultado.Verifica = this._CalculosService.Verifica(this.Resultado.Maximo_K_AdmisibleNumber, this.Resultado.Resistencia_TermicaNumber)
              }
            );

          }
      );    
    
    
    
    }catch(err){
      this.showError(err);
    }
  }
  ShowPopOver(){
        
  }
  LimpiarVariables(){
    this.oSelectedMaterial = null;
    this.ListaItems = [];
    this.msgs = [];
    this.Resultado = new BEResultado();
    this.InicializarVariables();
    this.oSelectedZona = this.lstZonas.find(x=>x.NombreZona === 'IIIB');
    
    
  }
}

