import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders} from '@angular/common/http';
import { GlobalEventsManagerService } from './global-events-manager.service';
import { ICalculos } from '../interfaces/ICalculos';
import { BEItem } from '../models/BEItem';
import { BaseService } from './base.service';
import { Observable } from 'rxjs/observable';
import { Subject } from 'rxjs/Subject';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};


@Injectable()

export class CalculosService extends BaseService implements ICalculos {
  oItem = new Subject<any>();
  list: any = [];
  _baseUrl = "";

  constructor(private httpClient: HttpClient,private configService: GlobalEventsManagerService) {
    super();
    this._baseUrl = configService.getWebApiURL(); 
  }

    CalcularResistenciaTermica(_ListaItems:Array<BEItem>, _EsMuro: boolean){
        let parametros = {ListaItems:_ListaItems, EsMuro: _EsMuro};
        try{
           
             let _url = this._baseUrl + '/api/calcular-resistencia-termica/';
             return this.httpClient.post(_url,parametros,httpOptions).catch(this.handleError);
        }catch(err){
             console.log(err);
        }
    
    }
    CalcularTransmitancia(_Resistencia: number){
      let parametros = {ResistenciaTermica: _Resistencia};
      try{
          
           let _url = this._baseUrl + '/api/calcular-transmitancia/';
           return this.httpClient.post(_url, parametros, httpOptions).catch(this.handleError);
      }catch(err){
           console.log(err);
      }
    }
    CalcularMasa(_ListaItems:Array<BEItem>){
      let parametros = {ListaItems: _ListaItems};
      try{
          
           let _url = this._baseUrl + '/api/calcular-masa/';
           return this.httpClient.post(_url, parametros, httpOptions).catch(this.handleError);
      }catch(err){
           console.log(err);
      }
    }
    CalcularKMaxAdmisible(_MasaMuro: number, _orientacion: string, _zona: string, _EsCubierta: boolean){
     
      let parametros = {MasaMuro: _MasaMuro, orientacion: _orientacion, zona: _zona, EsCubierta: _EsCubierta};
      try{
          
           let _url = this._baseUrl + '/api/calcular-k-max-admisible/';
           return this.httpClient.post(_url, parametros, httpOptions).catch(this.handleError);
      }catch(err){
           console.log(err);
      }
    }
    Verifica(KMaxAdmisible: number, Resistencia: number) {
         if (KMaxAdmisible === 0 && Resistencia == 0) return false;
       
         return (1/Resistencia) <= KMaxAdmisible;
     }

}
