import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders} from '@angular/common/http';
import { GlobalEventsManagerService } from './global-events-manager.service';
import { ISubtipoCalculo } from '../interfaces/ISubtipoCalculo';
import { BESubtipoCalculo } from '../models/BESubtipoCalculo';
import { BEResultadoCalculoMateriales } from '../models/BEResultadoCalculoMateriales';
import { BaseService } from '../services/base.service';
import { Observable } from 'rxjs/observable';
import { Subject } from 'rxjs/Subject';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';


const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};


@Injectable()

export class SubtipoCalculoService extends BaseService implements ISubtipoCalculo {
  oBESubtipoCalculo = new Subject<any>();
  list: any = [];
  _baseUrl = '';
  
  constructor(private httpClient: HttpClient,private configService: GlobalEventsManagerService) {
    super();
    this._baseUrl = configService.getWebApiURL(); 
  }

get(IdSubTipoCalculo) {
  
  try{
    let _url = this._baseUrl + '/api/obtener-subtipos-calculos/'+IdSubTipoCalculo.toString()+'/';
    return this.httpClient.get<BESubtipoCalculo[]>(_url).catch(this.handleError);
  }catch(err){
      console.log(err);
    }
  }
  CalcularMateriales(IdSubTipoCalculo, SuperficieVolumen) {
  
    try{
      let _url = this._baseUrl + '/api/calcular-materiales/'+IdSubTipoCalculo.toString()+'/'+SuperficieVolumen +'/';
      return this.httpClient.get<BEResultadoCalculoMateriales[]>(_url).catch(this.handleError);
    }catch(err){
  console.log(err);
      }
    }

}
