import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders} from '@angular/common/http';
import { GlobalEventsManagerService } from './global-events-manager.service';
import { IMaterial } from '../interfaces/IMaterial';
import { BEMaterial } from '../models/BEMaterial';
import { BaseService } from '../services/base.service';
import { Observable } from 'rxjs/observable';
import { Subject } from 'rxjs/Subject';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';


const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};


@Injectable()

export class MaterialService extends BaseService implements IMaterial {
  oMaterial = new Subject<any>();
  list: any = [];
  _baseUrl = '';
  
  constructor(private httpClient: HttpClient,private configService: GlobalEventsManagerService) {
    super();
    this._baseUrl = configService.getWebApiURL(); 
  }


//   getMateriales(): Observable<any> {
//       return this.oMaterial.asObservable();
//   }
getMateriales() {
  
  try{
    let _url = this._baseUrl + '/api/Material/obtener-materiales/';
    return this.httpClient.get<BEMaterial[]>(_url).catch(this.handleError);
  }catch(err){
console.log(err);
    }
  }

}
