import { Component, OnInit, EventEmitter, Input, Output } from '@angular/core';
import { HttpErrorResponse} from '@angular/common/http';
import { SubtipoCalculoService } from 'src/app/layout/services/subtipoCalculo.service';
import { Subscription } from 'rxjs/Subscription';
import { Message} from 'primeng/primeng';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import { Alert } from 'selenium-webdriver';
import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';
import { GlobalEventsManagerService } from 'src/app/layout/services/global-events-manager.service';
import { BESubtipoCalculo } from '../../models/BESubtipoCalculo';
import { BEResultadoCalculoMateriales } from '../../models/BEResultadoCalculoMateriales';
import {CheckboxModule} from 'primeng/checkbox';

@Component({
  selector: 'app-calculo-materiales',
  templateUrl: './calculo-materiales.component.html',
  styleUrls: ['./calculo-materiales.component.css']
})
export class CalculoMaterialesComponent implements OnInit {
  msgs: Message[] = [];
  subscription: Subscription;
  lstTrabajos:Array<BESubtipoCalculo>=[];
  lstResultado:Array<BEResultadoCalculoMateriales>;
  lstResultadoTotal:Array<BEResultadoCalculoMateriales>;
  ListaDeLista:Array<Array<BEResultadoCalculoMateriales>>;
  trabajoSelected: BESubtipoCalculo;
  altoIngresado: Number;
  espesorIngresado: Number;
  largoIngresado: Number;
  constructor(private _SubtipoCalculoService:SubtipoCalculoService) {  }

  ngOnInit(): void {
    this.Initialize();
    try { 
            this.subscription = this._SubtipoCalculoService.get(0).subscribe(
            request => {
                this.lstTrabajos = request;
            }
          );

    }catch (err) {
      this.showError(err);
    }
  }
  CalcularMateriales(){
   if(this.trabajoSelected == null || this.trabajoSelected == undefined){
    this.showError("Debe seleccionar un trabajo.");return;
   }
    let volumenSuperficie = 0.00;
    try { 
      this.subscription = this._SubtipoCalculoService.CalcularMateriales(this.trabajoSelected.IdSubTipoCalculo, (Number(this.altoIngresado)*Number(this.largoIngresado))).subscribe(
      request => {
          this.lstResultado = request;
      }
    );

}catch (err) {
this.showError(err);
}
  }
  showError(sErrorMensaje: string) {
    this.msgs = [];
    this.msgs.push({severity: 'error', summary: sErrorMensaje, detail: ''});
  }
  Initialize(){
    this.altoIngresado = null;
    this.espesorIngresado = null;
    this.largoIngresado = null;
  }

}
